module FlashAir
{
	    /**
     * internal use only. extra info  for each fileinfo
     */
    export interface FileInfoRawEx extends FileInfoRaw {
         _dom?: any;
         _syncStatus?: SyncStatus;
         _xhr?: XMLHttpRequest;
         _checked? : boolean;
    }
	
	export enum SyncScheme {
		SyncAll = 1,
		SyncNewOnes = 2
	}
	export enum SyncMode {
		None,
		Monitoring
	}
	export enum QueueStatus
	{
		Empty = 1,
		Queued = 2
	} 
	export enum SyncStatus {
		None,
		Queued,
		Syncing,
		Synced,
		Error
	}
	enum DirectoryStatus {
		Done,
		Pending,
		Running
	}
	/*class Directory {
		Path: string;
		Name : string;
		
	}*/
	export class FlashAirSynchronizer extends eventEmitter
	{
		localHomeDirectoryEntry  = null;
		//dirList: Array<Directory>;
		_queue: SyncQueue;
		fa: FlashAirClient;
		lastSyncChecked : Date = new Date();
		syncScheme : SyncScheme = SyncScheme.SyncAll;
		syncMode : SyncMode = SyncMode.None;
		queueStatus : QueueStatus = QueueStatus.Empty;
		
		constructor(fa : FlashAirClient){
			super();
			this.fa = fa;
			this._queue = new SyncQueue();
			//this.dirList = new Array<Directory>();
		}
		addMonitorDirectory( dir :string)
		{
			
		}
		removeMonitorDirectory(dir:string)
		{
			
		}
		/*
		getMonitorDirectories() : Array<Directory>
		{
			return this.dirList;
		}*/
		fileListUpaded( list : FileInfoRaw [], listAdded : FileInfoRaw [], listDeleted : FileInfoRaw [], scheme? : SyncScheme  )
		{
			var me = this;

	        console.log("listUpdated");
			//scheme = scheme || this.syncScheme;
			// check if remote files are already downloaded or not
			function readEntries(d, results){
				for( var i=0; i<results.length; i++ ){
					var entry : FileEntry | DirectoryEntry = results[i];
					if( entry.isDirectory ) continue; // ignore sub directories
					$.each( listAdded || list, function( j: number, item : FileInfoRawEx ){
						if( item.fname.toLowerCase() == entry.name.toLowerCase() ){
							item._syncStatus = SyncStatus.Synced;
							if( item._dom ){
								item._dom.addClass("downloaded");
							}
							return false;// to break;
						}
					});
				}
				
				if( results.length > 0 ){
					this.readEntries( readEntries.bind(this, d) );
					d = null;
				}
				if( d ) d.resolve();
			}
			function queueToSync(list, scheme){
				$.each(list, function(i,item : FileInfoRawEx){
					if( (!item._syncStatus || item._syncStatus == SyncStatus.None ) && !(item.attr & (FileAttribute.Directory + FileAttribute.Hidden )) ){
						if( scheme == SyncScheme.SyncAll 
								|| ( scheme == SyncScheme.SyncNewOnes && me.lastSyncChecked < FileInfo.FTIMEtoDateTime( item.fdate, item.ftime ) ) ){
							console.log("to be synced...");
							console.log(item);
							me.queue( item, true );
						}
					}
				});
				me.lastSyncChecked = new Date();
			}
			this.selectLocalHomeDirectory(true, false)
				.done(function(entry : DirectoryEntry ){
					var reader = entry.createReader();
					var d = $.Deferred()
						.done( function(){
							console.log("file listing has finished.");
							if( !scheme && me.syncMode != SyncMode.Monitoring ){
								console.log( "sync is not monitoring so skip queueing.");
							} else {
								queueToSync(listAdded || list, scheme || me.syncScheme );
							}
						});
					reader.readEntries(readEntries.bind(reader, d));
				});
			//
			// stop syncing if it's in deleted list
			//
			if( listDeleted && listDeleted.length > 0 ){
				$.each( listDeleted, function(i, item : FileInfoRawEx ){
					if( item._syncStatus == SyncStatus.Syncing ){
						console.log( "must stop syncing " + item.fname );
						me.unqueue(item);
					} else if ( item._syncStatus == SyncStatus.Queued ){
						me.unqueue(item);
					}
				});
				
			}
		}
		selectLocalHomeDirectory(silent :boolean = false, forceToSelect : boolean = false ) : JQueryPromise<any>
		{
			var me = this;
			var storage = chrome.storage.local;
			var d = $.Deferred();
			if( forceToSelect == true ){
				openHomeDirectorySelect(d);
				return d;
			}
			if( me.localHomeDirectoryEntry ){
				d.resolve(me.localHomeDirectoryEntry);
				return d;
			}
			storage.get("syncLocalHomeDir", function(items){
				var v = items["syncLocalHomeDir"];
				if( v ){
					chrome.fileSystem.isRestorable( v, function(can){
						if( can ){
							chrome.fileSystem.restoreEntry( v , function(_entry){
								me.localHomeDirectoryEntry = _entry;
								d.resolve(me.localHomeDirectoryEntry);
							});
						} else {
							openHomeDirectorySelect(d);
						}
					})
				} else {
					openHomeDirectorySelect(d);
				}
			});
			function openHomeDirectorySelect(d){
				if( silent ) d.reject();
				else {
				 	chrome.fileSystem.chooseEntry({ type: "openDirectory" }, 
						function (entry ) {
							if( entry ){
								me.localHomeDirectoryEntry = entry;	 
				                chrome.fileSystem.isWritableEntry(entry, function (r) {
				                    console.log("isWritableEntry = " + r);
				                });
								storage.set({"syncLocalHomeDir": chrome.fileSystem.retainEntry(entry) });
								d.resolve(me.localHomeDirectoryEntry);
							} else {
								console.log( chrome.runtime.lastError );
								d.reject();
							}
					 	});
			 	}
			}
	 		return d;
		}	

		//
		// Queue control
		//		
		private runningItem : FileInfoRaw;
		startMonitoring(){
			if( this.syncMode == SyncMode.None ){
				this.syncMode = SyncMode.Monitoring;
				this.startNext();
			}
		}
		stopMonitoring(){
			this.syncMode = SyncMode.None;
		}
		start( mode? : SyncMode ) : boolean {
			var started = this.startNext();
			if( started ){
				this.trigger("fa.sync.queue","started")
			}
			return started;
		}
		stop() : void {
			this.syncMode = SyncMode.None;
			this.unqueue(null); // cancell all;
		}
		startNext() : boolean {
			var me = this;
			if( ! me.runningItem ){
				var faItem : FileInfoRawEx = this._queue.next();
				if( faItem ){
					me.runningItem = faItem;
	                me.trigger("fa.sync.start", faItem );
					faItem._syncStatus = SyncStatus.Syncing;
					me.fa.pausePolling();
	                this.downloadFile( faItem )
	                    .progress( function(loaded, total ){
	                        me.trigger("fa.sync.progress", faItem, loaded, total );
	                    })
	                    .done(function(fileEntry){
	                        faItem._syncStatus = SyncStatus.Synced;
	                        me.trigger("fa.sync.done", faItem );
	                    })
						.fail(function(e){
							faItem._syncStatus = SyncStatus.Error;
	                        me.trigger("fa.sync.done", faItem );
						})
	                    .always(function(){
							me.runningItem = null;
							me.fa.resumePolling();
	               		  	me.start(); 
	                    });
					return true;
				} else {
					me.trigger("fa.sync.queue","empty");
				}
			}
			return false;
		}

		queue( item : FileInfoRaw, autoStart : boolean = false )
		{
			var status = (<FileInfoRawEx>item)._syncStatus;
			(<FileInfoRawEx>item)._syncStatus = SyncStatus.Queued;
			this._queue.add( item );
			if( autoStart ){
				if( !this.start() ){
					this.trigger('fa.sync.queued', item );
				}
			}
		}
		unqueue( _item : FileInfoRaw, forciblyAbort : boolean = true ){
			var item = <FileInfoRawEx>_item;
			var removed = false, stopped = false;
			if( item ){
				var status = item._syncStatus;
				removed = this._queue.remove( item ), stopped = false;
				if( forciblyAbort && item._xhr ){
					 item._xhr.abort(); delete item._xhr;
					 if( this.runningItem == item ){
						 this.runningItem = null;
					 }
					 stopped = true;
			 	}
				item._syncStatus = SyncStatus.None;
				if( removed || stopped){
					this.trigger("fa.sync.unqueued", item );
					if( this._queue.list.length == 0 ){
						this.trigger("fa.sync.queue", "empty");
					}
				}
			} else {
				while( this._queue.list.length > 0 ){
					this.unqueue(this._queue.list[ this._queue.list.length-1]);
				}
				if( this.runningItem ) this.unqueue(this.runningItem);
				// cancel all
			}
		}
		protected downloadFile( faItem : FileInfoRaw ) : JQueryPromise<any>
		{
			var me = this;
			var item = <FileInfoRawEx>faItem;
			var url = this.fa.baseUrl + item.r_uri + "/" + item.fname;
			var localPath = item.fname;
			var d = $.Deferred();
			this.selectLocalHomeDirectory()
				.then(function(entry){
					console.log( "Starting download..." + url );
				 	Loader.load( url, "blob"
	                	,function(fileData, fileInfo){
	                        entry.getFile(localPath,  {create: true}, function(fileEntry){
	                            fileEntry.createWriter(function(fileWriter){
	                                fileWriter.onwriteend = function(e){
	                                    fileWriter.onwriteend = function(e){
											d.resolve(fileEntry);
	                                    };
	                                    fileWriter.truncate(fileData.size);
	                                };
	                                fileWriter.onerror = function(e){
	                                  console.warn("Write failed: " + e.toString());  
	                                };
	                                fileWriter.write( fileData );
	                            });
	                        });
							delete item._xhr;
	                    }, function(e){ // error
	                        console.log(e);
							d.reject();
							delete item._xhr;
	                    }, function(loaded, total){ // progress
							d.notify( loaded, total);
						}, function(xhr){ // started
							item._xhr = xhr;
						}, function(e){ // aborted
							console.log( "transfer aborted.");
							delete item._xhr;
							d.reject();
						});
               
				}, function(e){
					d.reject();
				});
			return d.promise();
		}
	}
	class SyncQueue 
	{
		list : FileInfoRaw[] = [];
		next() : FileInfoRaw {
			return this.list.shift();
		}
		add( item :FileInfoRaw ){
			this.list.push(item);
		}
		remove( item : FileInfoRaw ) : boolean{
			var removed = false;
			for( var i=0; i<this.list.length; i++ ){
				var _item = this.list[i];
				if( _item.r_uri == item.r_uri && _item.fname == item.fname ){
					this.list.splice(i,1);
					removed = true;
					break;
				}
			}
			return removed;
		}
	}	
	class Loader {
		static callbacks = {

			/**
			 * Callback for loaded files.
			 * @param {string} source The remote file's URL.
			 * @param {Function} callbackSuccess The callback for successful loading.
			 * @param {Function} callbackError The callback for failed loading.
			 * @param {ProgressEvent} xhrProgressEvent The XHR progress event.
			 */
			onLoad: function(source, callbackSuccess, callbackError, xhrProgressEvent) {
			
			  // we have the file details
			  // so now we need to wrap the file up, including
			  // the caching information to return back
			  var xhr = xhrProgressEvent.target;
			  var fileData = xhr.response;
			  var fileInfo = RAL.CacheParser.parse(xhr.getAllResponseHeaders());
			
			  if(xhr.readyState === 4) {
			    if(xhr.status === 200) {
			      callbackSuccess(fileData, fileInfo);
			    } else {
			      callbackError(xhrProgressEvent);
			    }
			  }
			},
	
			/**
			 * Generic callback for erroring loads. Simply passes the progres event
			 * through to the assigned callback.
			 * @param {Function} callback The callback for failed loading.
			 * @param {ProgressEvent} xhrProgressEvent The XHR progress event.
			 */
			onError: function(callback, xhrProgressEvent) {
		  		callback(xhrProgressEvent);
			},
			onAbort: function(callback, e){
				callback(e);
			},
			onProgress: function(callback, e){
				if( e.lengthComputable){
					callback( e.loaded, e.total );
				}
				
			}
		};

	
		/**
		* Aborts an in-flight XHR and reschedules it.
		* @param {XMLHttpRequest} xhr The XHR to abort.
		* @param {Function} callbackSuccess The callback for successful loading.
		* @param {Function} callbackError The callback for failed loading.
		* @param {ProgressEvent} xhrProgressEvent The XHR progress event.
		*/
		static abort(xhr, source, type, callbackSuccess, callbackFail,callbackProgress, callbackStarted, callbackAborted) {
		
			// kill the current request
			xhr.abort();
			
			// run it again, which will cause us to schedule up
			Loader.load(source,  type, callbackSuccess, callbackFail, callbackProgress, callbackStarted, callbackAborted);
		}

		/**
		* Aborts an in-flight XHR and reschedules it.
		* @param {XMLHttpRequest} xhr The XHR to abort.
		* @param {string} type The response type for the XHR, e.g. 'blob'
		* @param {Function} callbackSuccess The callback for successful loading.
		* @param {Function} callbackError The callback for failed loading.
		*/
		static load(source, type, callbackSuccess, callbackFail, callbackProgress, callbackStarted, callbackAborted) {
		
			// check we're online, or schedule the load
			if(RAL.NetworkMonitor.isOnline()) {
			
			  // attempt to load the file
			  var xhr = new XMLHttpRequest();
			
			  xhr.onerror = Loader.callbacks.onError.bind(this, callbackFail);
			  xhr.onload = Loader.callbacks.onLoad.bind(this, source, callbackSuccess, callbackFail);
			  xhr.onprogress = Loader.callbacks.onProgress.bind(this, callbackProgress);
			  xhr.onabort = Loader.callbacks.onAbort.bind(this, callbackAborted);
			  xhr.open('GET', source, true);
			  xhr.responseType = type;
			  xhr.send();
			
			  // register our interest in the connection
			  // being cut. If that happens we will reschedule.
			  RAL.NetworkMonitor.registerForOffline(
			    Loader.abort.bind(this,
			      xhr,
			      source,
				  type,
			      callbackSuccess,
			      callbackFail,
				  callbackProgress,
				  callbackStarted,
				  callbackAborted));
				callbackStarted(xhr);
			} else {
			
			  // We are offline so register our interest in the
			  // connection being restored.
			  RAL.NetworkMonitor.registerForOnline(
			    Loader.load.bind(this,
			      source,
				  type,
			      callbackSuccess,
			      callbackFail,
				  callbackProgress,
				  callbackStarted,
				  callbackAborted));
			
			}
		}
	}
}
