var wlansd : FlashAir.FileInfoRaw[];
var faSync : FlashAir.FlashAirSynchronizer;
var fa : FlashAirApp;
//
// extends FlashAirApp
//
class FlashAirChromeApp extends FlashAirApp
{
    constructor(options: any){
        super(options);
    }
    initUi(){
        super.initUi();
        var me = this;
        $.when(
            $.get("app-component.html")
                .done(function(result){ $("body").append(result); } )
        ).then(function(){
            //
            // load localized text
            //
            $("*[data-localize]").each(function(index, el){
                var $el = $(el), val = $el.data('localize');
                if( val.text ){
                    $el.text( chrome.i18n.getMessage( val.text ));
                }
                if( val.html ){
                    $el.text( chrome.i18n.getMessage( val.html ));
                }
                if( val.attrs ){
                    for( var key in val.attrs ){
                        $el.attr( key, chrome.i18n.getMessage( val.attrs[key] ));
                    }
                }
            });
        });
        //
        //
        $("#headerLogoContainer a").click(function(e){
            e.preventDefault();
            hostSelectDialog(<FlashAirChromeApp> fa);
           return false; 
        });
       //
        // Sync appMenu
        //
        $("#syncStatusLine button[data-command], #navbar a[data-command]").on("click",function(e){
            var btn = $(this), cmd = btn.data("command");
            console.log('cmd : ' + cmd); 
            if( cmd == "folder"){
               faSync.selectLocalHomeDirectory( false, true )
                .done( me.homeDirectoryIsSet.bind(me) );
            } else if( cmd == "sync"){
                me.turnMonitoring( ! (faSync.syncMode == FlashAir.SyncMode.Monitoring) );
            } else if( cmd == "sync-cancel-all" ){
                $('#synStatusLine *[data-command="sync"]').removeClass("active btn-success");
                faSync.stop();
            } else if( cmd == "sync-mode-all"){
                btn.closest("li").addClass("active");
                btn.closest("ul").find('[data-command="sync-mode-news"]').closest("li").removeClass("active");
                faSync.fileListUpaded(wlansd, null, null, FlashAir.SyncScheme.SyncAll);
                faSync.start();
                faSync.syncScheme = FlashAir.SyncScheme.SyncAll;
            } else if( cmd == "sync-mode-news"){
                btn.closest("li").addClass("active");
                btn.closest("ul").find('[data-command="sync-mode-all"]').closest("li").removeClass("active");
                faSync.fileListUpaded(wlansd, null, null, FlashAir.SyncScheme.SyncNewOnes);
                faSync.start();
                faSync.syncScheme = FlashAir.SyncScheme.SyncNewOnes;
            } else if( cmd == "config"){
               
            } else if( cmd == "close"){
                window.close();
            }
        });
        //
        // grid item context menu
        //
        me.grid.on('mouseenter mouseleave', 'div.grid-item', function(e){
            var div = $(this);
            if( e.type == 'mouseenter') me.showContextMenuAt(div.data('fa-item'));
            else if( e.type == 'mouseleave') me.hideContextMenu();
        });
        $(document).on('click', '#fileItemMenu button[data-command]', function(e){
            var btn = $(this), cmd = btn.data("command");
            console.log( "command:" + cmd);
            var div = me.contextMenuTarget;
            if( div ){
                var faItem = div.data("fa-item");
                if( faItem ){
                    me.onItemCommand( faItem, cmd );
                }
            } 
        });
        //

        me.grid.on("click", "div.grid-item a.fileLink[href]", function (e) {
            var a = $(this), div = $(this).closest("div.grid-item"),
                imgContainerDiv = a.find("div.image-container");
            if( a.is(".dir-nav")) return;
            if( imgContainerDiv.is(".icon-photo")){
                var url = faClient.GetFileUrl( a.attr("href") );
                console.log( url );
                //imgContainerDiv.addClass("effect-pressed");
                //setTimeout(function(){ imgContainerDiv.removeClass("effect-pressed"); }, 200);
            } else {
                e.preventDefault();
                return false;
            }
            //window.open( url, "_blank");
            return false;
        });
        //
        // touch support
        //
        var hammer = new Hammer.Manager(me.grid.get(0));//new Hammer(me.grid.get(0));
        hammer.add(new Hammer.Pan({direction: Hammer.DIRECTION_ALL, threshold: 0, pointers: 0 }));
        hammer.add(new Hammer.Swipe({direction: Hammer.DIRECTION_ALL, threshold:0,velocity: 0.01 })).recognizeWith( hammer.get('pan') );
        hammer.add(new Hammer.Press({time:300})).recognizeWith([hammer.get('pan'),hammer.get('swipe')]);
        //hammer.add(new Hammer.Swipe()).recognizeWith(hammer.get('pan'));
        //hammer.get('swipe').set({ direction: Hammer.DIRECTION_VERTICAL });
        //hammer.get('press').recognizeWith( hammer.get('pan'));
        hammer.on("tap", function(e){
            console.log(e);
        });
        var touchTarget = null;
        hammer.on("press", function(e){
            var div = $(e.target).closest("div.grid-item");
            if( div.length > 0 ){
                var faItem : FlashAir.FileInfoRaw = div.data("faItem");
                if( !(faItem.attr & FlashAir.FileAttribute.Directory)) {
                    var ic = div.find(".image-container");
                    touchTarget = ic;
                    ic.css("transform","scale(0.9)");
                }
            }
            console.log(e.type);
        }).on("pressup",function(e){
            if( touchTarget){
                var ic = touchTarget;
                ic.css("transform","");
                touchTarget = null;
            }
            console.log(e.type);
        }).on("pan", function(e){
            if( touchTarget){
                touchTarget.css("transform","translate3d(" + e.deltaX + "px," + e.deltaY + "px,0)");
                e.preventDefault();
            }
            console.log(e.type);
        }).on("panend", function(e){
            if( touchTarget ){
                touchTarget.css("transform","");
                setTimeout( function(){ touchTarget = null; }, 500 );
            }
            console.log(e.type);
        }).on("swipedown", function(e){
            if( touchTarget ){
                var div = touchTarget.closest("div.grid-item");
                me.onItemCommand( div.data("faItem"), "download" );
                touchTarget = null;
            }
            console.log(e.type);
        });
        // go/back navigation
        $(document).on('keydown', function(e){
           if( e.keyCode == 8 ){
               console.log(e);
           } 
        })
        .on('mousedown', function(e){
               console.log(e);
        });
    }
    protected onItemCommand( faItem: FlashAir.FileInfoRaw, cmd : string  )
    {
        var item : FlashAir.FileInfoRawEx = faItem; 
        if( cmd == "download" ){
            if( item._syncStatus == FlashAir.SyncStatus.Syncing
                || item._syncStatus == FlashAir.SyncStatus.Queued ){
                faSync.unqueue(faItem);
            } else {
                faSync.queue(faItem, true);
            }
        } else if( cmd == "open"){
            var url = faClient.GetFileUrl( faItem.r_uri + "/" + faItem.fname );
            console.log( url );
            window.open( url, "_blank");
        }    
    }
    protected homeDirectoryIsSet(entry : DirectoryEntry ){
        console.log(entry);
        chrome.fileSystem.getDisplayPath(entry, function(displayPath){
            $("#localSyncPath").text(displayPath);     
        });
        $("#syncStatusLine").removeClass("no-directory");

    }
    protected contextMenuTarget;
    protected showContextMenuAt( _item : FlashAir.FileInfoRaw )
    {
        var item : FlashAir.FileInfoRawEx = _item;
        //if( ! faSync.localHomeDirectoryEntry ) return;
        var me = this;
        var menu = $("#fileItemMenu");
        var div = item._dom;
        if( !(item.attr & 16) ){ // not a directory
            me.contextMenuTarget = div;
            div.append(menu);
            var ic = div.find("div.image-container");
            menu.find('button[data-command="open"]').css("display", ic.is(".icon-photo") ? "" : "none");
            menu.show();
            var o = div.offset();
            menu.offset({ top: o.top, left: o.left });
        }
    }
    protected hideContextMenu(){
        var me = this;
        var menu = $("#fileItemMenu");
        menu.hide();
        $("body").append(menu);
    }
    protected turnMonitoring( turnOn : boolean )
    {
        var btn = $('#syncStatusLine *[data-command=sync]'),
            cls ="btn-success active";
        if( turnOn ){
            btn.addClass(cls);
            faSync.startMonitoring();
        } else {
            btn.removeClass(cls);
            faSync.stopMonitoring();
        }
    }
    navigate(dir : string) {
        var me = this;
        me.turnMonitoring(false);
        me.hideContextMenu();
        faSync.unqueue(null);
        super.navigate(dir);
    }
    setHostAddress(host : string, verify : boolean ){
        return this.client.setHostAddress(host, verify)
        .then(function(){
            chrome.storage.local.set({ faHost: host });
        });  
    }
    oninit()
    {
        super.oninit();
        this.initSyncApp();

    } 
    private initSyncApp()
    {
        var me = this;
        faSync = new FlashAir.FlashAirSynchronizer(me.client);
        faSync.selectLocalHomeDirectory( true, false )
            .done( me.homeDirectoryIsSet.bind(me) );
    
        faSync.on('fa.sync.progress', function(faItem : FlashAir.FileInfoRawEx, loaded, total){
           var div = faItem._dom;
           if( div ){
                var css = Math.floor(loaded*100/total) + "%";
                div.find(".progress-bar").css('width', css );
           }
        })  
        .on('fa.sync.queue', function(status){
            if( status == "empty"){
               // $('#navbar *[data-command="sync"]').removeClass("active btn-success");
               $("#fileTransferIndicator").removeClass("active");
                console.log("sync queue is empty.");
            } else if( status == "started"){
               $("#fileTransferIndicator").addClass("active");
                //$('#navbar *[data-command="sync"]').addClass("active btn-success");
                console.log("sync queue started running.");
            }
        })
        .on('fa.sync.queued', function(faItem: FlashAir.FileInfoRawEx ){
            var div = faItem._dom;
            if( div ){
                var progressDiv = div.find(".progress-bar");
                div.removeClass("downloaded downloading").addClass("queued");
                progressDiv.show().css("width", "0");
            }
        })
        .on('fa.sync.unqueued', function( faItem: FlashAir.FileInfoRawEx ){
            var div = faItem._dom;
            if( div ){
                var progressDiv = div.find(".progress-bar");
                div.removeClass("downloaded downloading queued");
                progressDiv.hide().css("width", "0");
            }
        })
        .on('fa.sync.start', function( faItem: FlashAir.FileInfoRawEx ){
            var div = faItem._dom;
            if( div ){
                var progressDiv = div.find(".progress-bar");
                div.removeClass("downloaded queued").addClass("downloading");
                progressDiv.show().css("width", 0);
            }
        })
        .on('fa.sync.done', function( faItem : FlashAir.FileInfoRaw ){
            var item : FlashAir.FileInfoRawEx = faItem;
           var div = item._dom;
           if( div ){
                var progressDiv = div.find(".progress-bar");
                if( item._syncStatus == FlashAir.SyncStatus.Error ){
                    div.removeClass("downloaded");
                    console.log("Error...");
                } else {
                    div.addClass("downloaded");    
                    console.log("Downloaded...");
                }
                progressDiv.hide();
           }
        })
        .on('fa.hostfailure',function(){
            console.log("Host failure");
            hostSelectDialog(<FlashAirChromeApp> fa);
        })
        .on('fa.hostchanged', function(){
            $("#hostUrl").text(fa.client.baseUrl);
            me.navigate("/");
        });
        
    }
    onListUpdated(listAll : FlashAir.FileInfoRaw[], listAdded : FlashAir.FileInfoRaw[], listDeleted : FlashAir.FileInfoRaw[])
    {
        faSync.fileListUpaded( listAll, listAdded, listDeleted );
    }
    pushHistory( dir : string )
    {
        // nothing...
    }
}


function hostSelectDialog(fa : FlashAirChromeApp )
{
    var dlg = $("#hostSelectorModal");
    $("#hostAddressTextBox").val(fa.client.baseUrl);
    dlg.modal();
    dlg.on('hide.bs.modal', function(e){
    });
    $("#saveHostAddressBtn").one('click',function(){
        var ValidIpAddressRegex = /^(https?\:\/\/|)((25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)(\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)){3})/;
        var ValidHostnameRegex = /^(https?\:\/\/|)([A-Za-z0-9]+(?:-[A-Za-z0-9]+)*(?:\.[A-Za-z0-9]+(?:-[A-Za-z0-9]+)*)*)/;
        var newUrl = $("#hostAddressTextBox").val();
        var m = ValidIpAddressRegex.exec(newUrl) || ValidHostnameRegex.exec(newUrl);
        if( m ){
            $("input,button", dlg).prop("disabled",true);
            fa.client.stopPolling();
            fa.setHostAddress( (m[1] || "http://") + m[2] , true)
            .done(function(){
                dlg.modal('hide'); 
            })
            .always(function(){
                $("input,button", dlg).removeProp("disabled");
            });
        }
    });
}